import React,{useState} from 'react'
import FUT from './FUT.js'
import './index.css'


export const packing = React.createContext()
export default function Main() {
    const [toggle, settoggle] = useState(true)
    const [packs, setpacks] = useState(false)
    
    let enter = () => {
        let text = document.querySelector(".title")
        let enterButton = document.querySelector('.enter')
        let select = text.children

        select[0].animate([
            {opacity: 1,
             top: "-10px"   },
            {opacity: 0,
              top:"-20px"}
        ],{
            duration: 500,
            iterations: 1,
            fill:'forwards',
        })
        select[1].animate([
            {opacity: 1,
             top: "200px"   },
            {opacity: 0,
              top:"0px"}
        ],{
            duration: 500,
            iterations: 1,
            fill:'forwards',
        })
        select[2].animate([
            {opacity: 1,
             top: "400px"   },
            {opacity: 0,
              top:"0px"}
        ],{
            duration: 2000,
            iterations: 1,
            fill:'forwards',
        })
        enterButton.animate([ 
            {height:"0px",
             width:"0px",
            pointerEvents:'none',
            borderStyle:"none"  }

        ],
        {duration:500,
        iterations:1,
        fill:"forwards"}
        )
        setTimeout(()=>settoggle(!toggle),2500)
        setpacks(!packs) 
    
    }   
    return (
        <main > 
          { toggle ? 
            <section className='background' >
                <article className='border' >
                <h1 className='title' ><span className='ultimate' >Ultimate</span> <span className='pack' >Pack</span><span className='opening' > Opening</span></h1>
                </article>
                <button className='enter' onClick={()=>enter()}  >      </button>
            </section> :
             
            <packing.Provider value={[packs,setpacks]} >
            <FUT/>
            </packing.Provider>
            }
        </main>
    )
}
