import React,{useState,useContext,useEffect} from 'react'
import Info from './Info'
import {packing} from './Main'

export default function FUT() {
    const [cards, setcards] = useState([])
    const [packs,setpacks] = useContext(packing)
    const [open, setopen] = useState(false)
    useEffect(() => {
        
    
        if (open === false)  {
            let packs = document.querySelector('.Packs')
            let allPacks = packs.children
            setTimeout(()=>{
              for (let i = 0; i < allPacks.length; i++) {
                   allPacks[i].animate([{
                    opacity:1,
                    marginTop:"300px",
                }],
                {
                  duration:1000,
                  iterations:1,
                  fill:'forwards'

                })
                setTimeout(()=>{
                  allPacks[i].style.pointerEvents = "all"   
                },1500)     
              } 
            },1000)           
        }
        console.log(`test`);
    }, [open])
    useEffect(() => {
        let deck = document.querySelector('.deck')

        if (deck) {
             deck.children[0].animate([{
                opacity:1,
                top:"0px",
                left:"33%"

            }],{
            duration: 1000,
            iterations: 1,
            fill:'forwards',
            })

         setTimeout(()=>{
             deck.children[1].animate([{
                opacity:1,
                top:"0px",
                left:'66%'

            }],{
            duration: 1000,
            iterations: 1,
            fill:'forwards',
            })},1000)
            
        }
    }, [open])

    let goldPack = () => {     
        // randomization
        let filter = Info.filter(stuff =>   stuff.color === "gold"  )
        let rngArray = filter.sort(()=>Math.random() - 0.5)
        let randomNum = Math.floor(Math.random() * rngArray.length )
        if (randomNum > rngArray.length - 2 ) {
            randomNum = rngArray.length - 2
        }
        let secondRandomNum = randomNum + 2
        let estimate = rngArray.slice(randomNum,secondRandomNum)
        setcards(estimate)
        setopen(true)
    }
    let silverPack = () => {
        let filter = Info.filter(stuff =>   stuff.color === "silver"  )
        let rngArray = filter.sort(()=>Math.random() - 0.5)
        let randomNum = Math.floor(Math.random() * rngArray.length )
        if (randomNum > rngArray.length - 2 ) {
            randomNum = rngArray.length - 2
        }
        let secondRandomNum = randomNum + 2
        let estimate = rngArray.slice(randomNum,secondRandomNum)
        setcards(estimate)
        setopen(true)
    }
    let bronzePack = () => {
        
       let filter = Info.filter(stuff =>   stuff.color === "bronze"  )
        let rngArray = filter.sort(()=>Math.random() - 0.5)
        let randomNum = Math.floor(Math.random() * rngArray.length )
        if (randomNum > rngArray.length - 2 ) {
            randomNum = rngArray.length - 2
        }
        let secondRandomNum = randomNum + 2
        let estimate = rngArray.slice(randomNum,secondRandomNum)
        setcards(estimate)
        console.log(estimate);
        setopen(true)
    }
    let reset = () => {
        setcards([])
        setopen(false)
    }  
    return (
        <section>
            {open ? <article className='deck' >
            {
                cards.map((data,index)=>{
                    const {player,rarity,position} = data;
 
                    return( 
                    <div style={{backgroundColor:rarity}}  key={index} className='card'>
                        <h1>{player}</h1>
                        <h2>{position}</h2>
                        
                    </div>)
                })
                
            }
             <button onClick={() =>reset()}>Open Packs  </button>
            
            </article> : <article>
                <div className='Packs' >  
                <button className='bronzePack' onClick={()=>bronzePack()} >Bronze</button>
                <button className='silverPack' onClick={()=>silverPack()}  >Silver</button>
                <button className='goldPack' onClick={()=>goldPack()}  >Gold</button>
                </div>
            </article>}
                
        </section>
    )
}
